import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import '../../App.scss';
import GooglePlacesAutocomplete, {
  geocodeByPlaceId,
} from 'react-google-places-autocomplete';

const HomeAddressForm = () => {
  const [address, setAddress] = useState({ addressComponents: '' });

  const onChange = e => {
    setAddress({ ...address, [e.target.name]: e.target.value });
    console.log('e', e.target.value);
  };

  const getPlaceDescription = async description => {
    console.log('Place Description: ', description);
    const { place_id } = description;
    // const buildingVillaName = description.structured_formatting.main_text;

    // setAddress({ ...address, buildingVillaName: buildingVillaName });

    try {
      const results = await geocodeByPlaceId(place_id);
      let addressComponents = {
        street_number: '',
        route: '',
        locality: '',
        administrative_area_level_1: '',
        country: '',
        postal_code: '',
      };

      results[0].address_components.forEach((item, index) => {
        // const types = item.types[0];
        const typeValue = item.long_name;
        addressComponents[item.types[0]] = typeValue;

        console.log('item', item);
        console.log('addressComponents', addressComponents);
      });

      setAddress({ ...address, addressComponents });
      console.log('Results', results);
      console.log('current address', address);
    } catch (err) {
      console.error(err);
    }
  };

  return (
    <div className='container'>
      <h1>Home Country Address</h1>

      <div className='form'>
        <label htmlFor=''>Your Address</label>
        <GooglePlacesAutocomplete
          onSelect={console.log}
          placeholder='Start typing your address..'
          onSelect={description => getPlaceDescription(description)}
        />

        <label htmlFor=''>Apartment / House No.</label>
        <input type='text' />

        {/* <label htmlFor=''>Building name / Villa</label>
        <input type='text' value={address.buildingVillaName} /> */}

        <label htmlFor=''>City / Area</label>
        <input
          type='text'
          name='locality'
          value={address.addressComponents.locality}
          onChange={e => onChange(e)}
        />

        <label htmlFor=''>Country</label>
        <input
          type='text'
          name='country'
          value={address.addressComponents.country}
          onChange={e => onChange(e)}
        />
      </div>

      <div className='footer-btns'>
        <Link to='/' className='back-btn'>
          Back
        </Link>

        <Link to='/home-address-form' className='next-btn'>
          Next
        </Link>
      </div>
    </div>
  );
};

export default HomeAddressForm;
