import React, { useState } from 'react';
// import { TranslatorProvider, useTranslate } from 'react-translate';
import { Link } from 'react-router-dom';
import '../../App.scss';
import GooglePlacesAutocomplete, {
  geocodeByPlaceId,
} from 'react-google-places-autocomplete';

const ResidenceAddressForm = () => {
  const [address, setAddress] = useState({
    buildingVillaName: '',
    city: '',
  });

  const onChange = e => {
    setAddress({ ...address, [e.target.name]: e.target.value });
    console.log('e', e.target.value);
  };

  const getPlaceDescription = async description => {
    console.log('Place Description: ', description);
    const { place_id } = description;
    const buildingVillaName = description.structured_formatting.main_text.toString();

    setAddress({ ...address, buildingVillaName: buildingVillaName });
    console.log('current address', address);

    try {
      const results = await geocodeByPlaceId(place_id);

      results[0].address_components.forEach(item => {
        const type = item.types[0];

        switch (type) {
          case 'locality':
            setAddress({ ...address, city: item.long_name });
            break;
          default:
            break;
        }

        console.log('item', item);
      });

      const place = results[0].address_components[0].long_name;
      console.log('Results', results);
      console.log('place', place);
    } catch (err) {
      console.error(err);
    }
  };

  return (
    <div className='container'>
      <h1>Residence Address</h1>

      <div className='form'>
        <label htmlFor=''>Your Address</label>
        <GooglePlacesAutocomplete
          autocompletionRequest={{
            componentRestrictions: {
              country: ['ae'],
            },
          }}
          placeholder='Start typing your address..'
          onSelect={description => getPlaceDescription(description)}
        />

        <label htmlFor=''>Apartment / Villa No.</label>
        <input type='text' />

        {/* <label htmlFor=''>Building name / Villa</label>
        <input type='text' value={address.buildingVillaName} /> */}

        <label htmlFor=''>City</label>
        <input
          type='text'
          name='city'
          value={address.city}
          onChange={e => onChange(e)}
        />
      </div>

      <div className='footer-btns'>
        <Link to='/home-address-form' className='next-btn'>
          Next
        </Link>
      </div>
    </div>
  );
};

export default ResidenceAddressForm;
